.button, .active {
    font-size: 1rem;
    padding: 5px;
    outline: none;
    border: none;
}

.button {
    background: #222;
    color: #aaa;
}

.active {
    background-color: #777;
    color: #222;
    font-weight: bold;
}

.button:hover, .active:hover {
    cursor: pointer;
}
